@import url('https://fonts.googleapis.com/css2?family=Jost:wght@400;600&family=Karla:ital,wght@1,300&display=swap');

body, html {
  background-image: url(./img/weather.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position:relative;
  padding: 0;
  margin: 0;
  vertical-align: baseline;
  width: 100%;
  height: 100%;
  font-family: 'Karla', sans-serif;
}

h1, h2, h3, h4{
  font-family: 'Jost', sans-serif;
}

#root {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  margin: 0 auto;
}

.wrapper-bg {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 700px;
  height: 300px;
  background-color: rgba(255, 255, 255, 0.5);
}

.wrapper-absolute {
  text-align: center;
}

.title {
  font-size: 1.5rem;
  background-color: #fff;
  padding: 0.3rem;
  border-radius: 3px;
}

.clouds,
.clear,
.snow,
.rain,
.fog {
  top:0;
  left: 0;
  width: 100%;
  opacity: 0.8;
  position: absolute;
  margin: 0 auto;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin:0;
  padding: 0;
}

.clouds {
  background-image: url(./img/cloud.jpg);
}

.clear {
  background-image: url(./img/clear.jpg);
}

.snow {
  background-image: url(./img/snow.jpg);
}

.rain {
  background-image: url(./img/rain.jpg);
}

.fog {
  background-image: url(./img/fog.jpg);
}

.weather-wrapper {
  padding: 5rem 8rem;
  opacity: 0.9;
  border-radius: 5px;
  background-color: #67AFCB;
  margin-top: 10px;
}

table, th, td {
  border-bottom: 1px solid #bababa
  ;
  border-collapse: collapse;
  padding: 1rem;
  text-align: center;
}

th {
  background-color: #092834;
  color: #E4F1F6;
  font-size: 1.2rem;
}

td {
  font-size: 1.1rem;
}

table {
  max-width: 90%;
  line-height: 1.5rem;
  margin: 0 auto 1.5rem;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}

caption {
  font-size: 1.3rem;
  margin: 1.5rem 0;
  font-weight: bold;
  font-size: 1.5rem;
}

td:nth-child(1) {
  font-weight: bold;
  font-size: 1.2rem;
  padding: 1rem;
  color: #E4F1F6;
  background-color: #347B98;
}

td:nth-child(1) div:nth-child(2) {
  font-size: 1rem;
  margin-top: 0.5rem;
}

.city-list-wrapper {
  position: absolute;
  background-color: #E4F1F6;
  color: #2483A8;
  z-index: 1;
  width: 300px;
  padding-bottom: 0.4rem;
}

.input-width {
  width: 300px;
}
.city-list {
  font-size: 1rem;
  padding-top: 0.4rem;
  cursor: pointer;
}

.city-list:hover {
  color:#092834;
}

.click-button {
  background-color: #E4F1F6; 
  border: none;
  color: #1A3E4C;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
  padding: 10px 24px;
  border-radius: 2px;
  display: flex;
  margin: 1rem auto;
  
  cursor: pointer;
}

.click-button:hover {
  background-color: #2483A8;
  color: #E4F1F6;
}

.forecast-weather {
  margin-top: 1rem
}

.project-language {
  margin-bottom: 1rem;
  color: #3E4676;
}

